.section{
    
    border: 1px gray solid;
    width :40rem ;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 25rem;
    
}
.home{
   
    display: flex;
    /* align-items: flex-start;
    justify-content: flex-start; */
    margin-bottom: 2.3rem;
    font-weight: 700;
    font-size: 1.2rem;
    background:transparent;
}
.home>span{
    background: transparent;
}
.home>img{
    display: none;
}
.nav{
    border:1px rgb(147, 145, 145) solid;
    width: 40rem;
    /* padding: rem; */
    margin-bottom: 2rem;
    position: fixed;
    background:transparent;
    backdrop-filter: blur(20px);
    
}
.icon{
    color: rgb(29, 155, 240) ;
    cursor: pointer;
}
.img{
   
    height: 5vh;
    width: 2.5vw;
    border-radius: 100px;
}
.top{
    display: flex;
    align-items: center;
    justify-content: center;
 
}
.forYou{
    
    background:transparent;
    position: sticky;
    top: 6.1rem;
    display: flex;
    flex: 2;
    align-items: center;
    justify-content: center;

}
.div1{
    background:transparent; 
display: flex;
align-items: center;
justify-content: center;
width:20vw;
font-size: 1rem;
font-weight: 700;
padding: 1rem;


}
.div1:hover{
    background-color: rgb(218, 210, 210);
}
.div2{
    /* border:2px rgb(128, 45, 0) solid; */
    margin-top: 9rem;
    
    display:flex;
    
    flex-direction: column;
}
.div3{
    
    background:transparent;
  display: flex;
align-items: center;
justify-content: center;
width:20vw;
font-size: 1rem;

padding:1rem;
}
.div3:hover{
    background-color: rgb(218, 210, 210);
}
.btn{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 3.5rem;
}
.imageWrapper{
    max-height: 20rem;
    overflow: auto;
}
.footericon{
    display:none;
}
.mobile{
    display: none;
}
@media screen and (max-width:770px) {
    .section{
        margin-left: 5rem;
    }
    .home{
        display:none
    }

}
@media screen and (max-width:450px) {
    .section{
        margin-left: 0.2rem;
    } 
    .mobileicon{
        position: fixed;
       border-radius: 100px;
       margin-left: 34rem;
       margin-top: 120%;
      }
      .btn{
        display:none
      }
      .top{
        display:none
      }
    .footericon{
        display:flex;
        justify-content: space-around;
        position:sticky;
        bottom:0;
        height:3rem;
        width:100%;
        
    }
    .drawer{
        border:2px solid red;
        display:flex;
        top:0;
    }
    .mobile{
        display: flex;
    }

}